@import './buttons.css' ;

:root{
    --fluid-container-width: calc((100vw - (100vw - 100%)));
    --boxed-container-width: calc(var(--fluid-container-width)*0.5);
}

body,
body * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

/* CUSTOM CSS */

.grid-wrapper {
    display: flex;
    justify-content: center;
    width: var(--fluid-container-width);
    min-height: 100vh;
    background-color: white;
}

.grid-wrapper > .section {
    display: inherit;
    justify-content: inherit;
    width: inherit;
    border: 2px dashed rgb(255, 0, 0);
}

.grid-wrapper > .section > .article {
    width: var(--boxed-container-width);
    border: 2px dashed rgb(0, 0, 0);
}

#user-form {
    display: flex;
    flex-direction: column;
    margin: 20px 0 20px 0;
}

#user-form {
    display: grid;
    justify-content: center;
}

table#view-ui--data {
    display: flex;
    flex-direction: column;
}

table#view-ui--data > tr {
    display: inherit;
    justify-content: space-evenly;
}

table#view-ui--data > tr > th, td{
    width: 15%;
    word-wrap: break-word;
    padding: 10px;
    /* border: 1px dashed black; */
}

header#add-ui--header > * {
  margin: 10px;
}

/*  */

form > * {
  margin: 10px !important;
}

button {
    padding: 5px 10px;
}

/* MEDIA QUERIES :  */

@media only screen and (max-width: 767px) {
    body {background-color: yellow}
    table#view-ui--data {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
      }
      
      table#view-ui--data > tr {
        display: inherit;
        flex-direction: inherit;
        justify-content: space-evenly;
      }
      
      table#view-ui--data > tr > th, td {
        width: 100% !important;
        word-wrap: break-word !important;
        text-align: center !important;
      }
      button {
          width: 100%;
      }
}
  