/* BUTTONS */

.title-windows95 {
    background:#C0C0C0;
    outline:1px solid #000000;
    background:#C0C0C0;
    border-width:1px;
    border-style:solid;
    border-color:#FFFFFF #808080 #808080 #FFFFFF;
    border-radius: 1px;
    font-family:"MS Sans Serif", serif;
    font-weight: 400;
    text-align:center;
    color: #000;
}

.title-windows95:hover {
    border-color:#808080 #FFF #FFF #808080;
}

.title-hacker {
    border-radius: 0;
    background: #000;
    font-family: monospace;
    color: #0f0;
  }

.title-glitch {
    background: repeating-linear-gradient(
      0deg,
      #000,
      #000 1px,
      #555 1px,
      #555 2px
    );
    color: white;
    text-shadow: -1px 0 #00fff9, 1px 2px #ff00c1;
    animation: glitch 1s infinite;
    -moz-animation:glitch 1s infinite; /* Firefox */
    -webkit-animation: glitch 1s infinite; /* Safari and Chrome */
    -o-animation: glitch 1s infinite; /* Opera */
  }
  
  @keyframes glitch
  {
  0%   {text-shadow: -1px 0 #00fff9, 1px 2px #ff00c1;}
  50%  {
      text-shadow: -1px -1px #ff00c1, 1px 1px #00fff9;
      background: repeating-linear-gradient(
        0deg,
        #777,
        #777 1px,
        #111 1px,
        #111 2px
      );
    }
  66%  {text-shadow: -1px 0px #00fff9, 1px 0px #ff00c1;}
  83%  {text-shadow: 1px 0px #00fff9, -1px 0px #ff00c1;}
  }
  
  @-moz-keyframes glitch /* Firefox */
  {
  0%   {text-shadow: -1px 0 #00fff9, 1px 2px #ff00c1;}
  50%  {
      text-shadow: -1px -1px #ff00c1, 1px 1px #00fff9;
